* {
    margin: 0;
    padding: 0;
    border: 0;
    box-sizing: border-box;
}

body {
    background: var(--white);
}

h1, h2, h3, h4, h5, h6, p {
    margin: 0;
}

a {
    text-decoration: none;
    cursor: pointer;
}

ul {
    list-style: none;
}

img {
    max-width: 100%;
}

header {
    padding: 20px 30px;
    font-family: arial, helvetica, sans-serif;
    color: #fff;
    background: #F55A5A;
}

header .header-content {
    max-width: 1200px;
    width: 100%;
    margin: 0 auto;
}

#locations {
    padding: 30px 0;
}

#locations .locations-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 30px;
    max-width: 1200px;
    width: 100%;
    margin: 0 auto;
}

#locations .locations-list li {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    flex: 0 0 48%;
}

#locations .locations-list li .location-img-container {
    flex: 1 0 auto;
    width: 200px;
    height: 250px;
    margin-right: 20px;
}

#locations .locations-list li .location-img-container img {
    width: 200px;
    height: 250px;
    object-fit: cover;
    border-radius: 5px;
}

#locations .locations-list li .location-info-container {
    font-family: arial, helvetica, sans-serif;
}

#locations .locations-list li .location-info-container .loaction-info-place span a {
    margin-left: 5px;
    font-size: 11px;
    color: #918E9B;
}

#locations .locations-list li .location-info-container .location-title {
    margin-bottom: 15px;
}

footer {
    padding: 15px 30px;
    font-family: arial, helvetica, sans-serif;
    color: #fff;
    background: #F55A5A;
}

footer .flex-footer-content {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    max-width: 1200px;
    width: 100%;
    margin: 0 auto;
}

footer .flex-footer-content p {
    flex: 1;
    font-size: 14px;
    line-height: 25px;
}

footer .flex-footer-content p.about-project {
    text-align: left;
}

footer .flex-footer-content p.contribute-and-copyright {
    text-align: right;
}

footer .flex-footer-content p a {
    color: #fff;
    text-decoration: underline;
}